@import "main-module";

.counter {
    background-color: #fff;
    padding: 100px 0;

    @include below(1380px) {
        padding: 60px 0;
        overflow: hidden;
    }
 
    &__wrap {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;

        .swiper-wrapper {
            width: 100%;
            justify-content: space-between;
            display: flex;

            @include below(1024px) {
                padding-bottom: 40px;
            }
        }

        .swiper-slide {
            display: flex;
            width: auto;
        }

        .swiper-pagination {
            top: auto !important;
            bottom: 0 !important;
            .swiper-pagination-bullet {
                width: 12px;
                height: 12px;
                margin: 0 8px !important;
            } 
            
            .swiper-pagination-bullet-active {
                background: #00D54B;
            }
        }
    }

    &__col {
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        padding: 0 25px;
        flex: auto;

        @include below(1200px) {
            padding: 0 15px;
        }

        @include below(420px) {
            padding: 0 25px;
        }

        &::after,
        &::before {
            content: "";
            position: absolute;
            top: 20px;
            right: 0;
            width: 1px;
            height: 130px;
            background-color: #d8d8d8;

            @include below(1200px) {
                height: 80%;
            }

            @include below(420px) {
                right: 15px;
            }
        }

        &::before {
            left: 0;
            right: auto;

            @include below(420px) {
                left: 15px;
            }
        }
    
        @include above(421px) {
            &::before {
                content: none;
            }
        }

        &:nth-child(even) {
            .counter__count {
                color: #0be05c;
            }
        }
    }

    &__count {
        font-size: 80px;
        font-style: normal;
        font-weight: 700;
        line-height: 100%;
        width: 100px;
       
        display: flex;
        justify-content: center;

        @include fluid-type(768px, 1920px, 32px, 80px);

        @include below(420px) {
            font-size: 90px;
        }
    }

    &__numb {
        
    }

    &__title {
        font-size: 22px;
        font-weight: 700;
        line-height: 100%;
        text-transform: uppercase;
        margin-top: 15px;
        text-align: center;

        @include fluid-type(769px, 1920px, 14px, 22px);

        @include below(420px) {
            font-size: 24px;
        }
    }
}