@import "main-module";

.video-block {
    position: relative;
    padding-bottom: 56%;

    @include below(768px) {
        padding-bottom: 75%;
    }

    iframe {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 100;
        width: 100%;
        height: 100%;
    }
}