@import "main-module";

.algorithm {
    position: relative;
    z-index: 10;
    overflow: hidden;

    &__row {
        display: flex;
    
        @include below(1024px) {
            flex-wrap: wrap;
        }
    }

    &__left { 
        width: 40%;
        position: relative;

        @include below(1700px) {
            width: 33.333%;
        }
   
        @include below(1024px) {
            width: 100%;
            height: 568px;
        }
    }

    &__tabber {
        position: relative;
        width: 60%;
        display: flex;

        @include below(1700px) {
            width: 66.66666%;
        }

        @include below(1024px) {
            width: 100%;
        }
    }

    &__title {
        position: absolute;
        top: 183px;
        left: 242px;
        color: #FFF;
        text-shadow: 0px 0px 15px rgba(0, 0, 0, 0.40);
        font-size: 50px;
        font-weight: 700;
        line-height: 100%; 

        @include below(1800px) {
            left: 160px
        }

        @include below(1700px) {
            left: 100px;
        }

        @include below(1600px) {
            left: 80px;
            font-size: 45px;
        }

        @include below(1500px) {
            left: 40px;
        }

        @include below(1340px) {
            font-size: 40px;
        }

        @include below(1240px) {
            text-align: center;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        @include below(768px) {
            font-size: 36px;
            br {
                display: none;
            }
        }

        span {
            color: #00D54B;
        }
    }

    &__img {
        width: 100%;
        height: 100%;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__list {
        width: 43%;
        background-color: #fff;

        @include below(1024px) {
            width: 50%;
        }

        @include below(700px) {
            width: 100%;
        }
    }

    &__item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: #000;
        padding: 35px;
        font-weight: 700;
        line-height: 140%; 
        text-decoration-line: underline;
        transition: color .3s linear, background-color .3s linear;

        cursor: pointer;
        min-height: 144px;

        @include fluid-type(768px, 1920px, 18px, 20px);

        &.is-active {
            @include above(701px) {
                background-color: rgba(0, 213, 200, 0.10);
                color: #00867E;
            }
        }

        &.is-mb-active {
            @include below(700px) {
                color: #00D54B;
                background-color: rgba(0, 213, 75, 0.20);
            }
        }

        &:hover:not(:active) {
            color: #00867E;
            @include below(700px) {
                color: #00D54B;
            }
        }


        @include below(768px) {
            font-size: 18px;
            &:hover {
                background-color: rgba(0, 213, 75, 0.20);
                color: #00D54B;
            }

            &:not(:last-child) {
                border-bottom: 1px solid #CCF7DB;
            }
        }

        @include below(540px) {
            padding: 40px 15px 30px;
        }

        span {
            max-width: 305px;

            @include below(768px) {
                max-width: initial;
            }
        }

        svg {
            width: 39px;
            height: 39px;
            flex-shrink: 0;
            margin-left: 15px;
        }
    }

    &__body {
        position: relative;
        width: 57%;

        @include below(1024px) {
            width: 50%;
            height: 100%;
        }

        @include below(700px) {
            position: absolute;
            right: 0;
            width: 100%;
            transition: transform .3s linear;
            transform: translateX(150%);

            &.is-show {
                transform: translateX(0);
            }
        }
    }

    &__right {
        
        &:not(.is-active) {
            display: none;
        }


        padding: 60px;
        background-color: #00D5C8;
        color: #fff;
        width: 100%;
        height: 100%;

     
        @include below(1500px) {
            padding: 40px;
        }

       

        @include below(768px) {
            width: 100%;
            background: linear-gradient(1deg, #00D54B 0%, #000 100%);
        }

        @include below(540px) {
            padding: 55px 15px 85px;
        }
    }

    &__content-title {
        font-size: 20px;
        font-weight: 700;
        line-height: 140%; 
        margin-bottom: 30px;
        @include fluid-type(768px, 1920px, 16px, 20px);
        @include below(768px) {
            font-size: 16px;
            line-height: 120%;
        }

        a {
            color: #fff;
            text-decoration: underline;
        }
    }

    &__content-text {
        font-size: 17px;
        font-weight: 500;
        line-height: 130%;
        @include fluid-type(768px, 1920px, 14px, 17px);
        @include below(768px) {
            font-size: 14px;
            line-height: 120%;
        }
    }

    &__back-link {
        margin-bottom: 20px;
        
        @include above(701px) {
            display: none;
        }
        
        svg {
            width: 39px;
            height: 39px;
        }
    }
}